/* aldrich-regular - latin */
@font-face {
  font-family: 'Aldrich';
  font-style: normal;
  font-weight: 400;
  src: url(../../static/media/aldrich-v8-latin-regular.c0a64bc6.eot); /* IE9 Compat Modes */
  src: local('Aldrich Regular'), local('Aldrich-Regular'),
       url(../../static/media/aldrich-v8-latin-regular.c0a64bc6.eot?#iefix) format('embedded-opentype'), 
       url(../../static/media/aldrich-v8-latin-regular.f2ba1dee.woff2) format('woff2'), 
       url(../../static/media/aldrich-v8-latin-regular.33b0bcc0.woff) format('woff'), 
       url(../../static/media/aldrich-v8-latin-regular.8e319a5f.ttf) format('truetype'), 
       url(../../static/media/aldrich-v8-latin-regular.08310f05.svg#Aldrich) format('svg'); /* Legacy iOS */
}

html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}

body > header,
body > footer {
  font-family: 'Aldrich', sans-serif;
  height: 50px;
}

body > header {
  padding: 10px;
  font-size: 1.5em;
  background-color: #ffe999;
}

body > footer {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #bec4e3;
  border-top: 3px solid #162162;
  text-align: center;
}

body > footer a {
  color: #162162;
}

body > header a {
  float: right;
  margin-left: 5px;
  text-decoration: none;
  color: #162162;
}

#root {
  flex: 1 1;
}

.app {
  height: 100%;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.gs-header {
  color: #162162;
  font-family: 'Aldrich', sans-serif;
  background-color: #ffe999;
  font-size: 3em;
  padding-bottom: 10px;
  text-align: center;
}

.gs-settings {
  background-color: #ffde66;
  border-bottom: 3px solid #ffc800;
  text-align: center;
}

.gs-settings .ant-switch-checked {
  background-color: #162162;
}

.gs-settings .ant-form-item-label,
.gs-settings .ant-form-item-children,
.gs-settings .ant-radio-group {
  color: #162162;
}

.logo {
  height: 60px;
}

.main-content {
  color: #162162;
  display: flex;
  flex: 1 1;
}

.main-content h2 {
  color: #162162;
}

.gui-wrapper {
  margin: 10px 5px 10px 10px;;
  background-color: #e8e8e8;
  flex: 3 1;
}

.gui-wrapper h2,
.editor-wrapper h2 {
  font-family: 'Aldrich', sans-serif;
  text-align: center;
  color: #162162;
}

.editor-wrapper {
  margin: 10px 10px 10px 5px;
  background-color: #e8e8e8;
  flex: 2 1;
  display: flex;
  flex-direction: column;
  max-width: 34%;
}

/* Small devices (landscape phones, less than 768px) */
@media (max-width: 767.98px) {
  .gs-small-col {
    width: auto;
  }

  .main-content {
    display: block;
  }

  .editor-wrapper {
    max-width: 95%;
  }

}

